  /* util */
  



.flex-1{
    flex: 1;
}
.a-i{
    align-items: center;
}
.btn{
   padding: 0.5rem 2rem;
   font-weight: 700;
   border: 3px solid transparent; 
   cursor: pointer; 
   color: var(--dark);
   transition:0.4s ease-in-out ;
   text-transform: capitalize;
}
.btn-primary{
    background-color: var(--primary);
    
}
.btn-primary:hover{
    background-color: var(--dark);
    border-color: var(--primary);
    color: var(--primary);

}
.btn-secondary{
    background-color: transparent;
    color: var(--primary);
    border-color: var(--primary);

}
.btn-secondary:hover{
    background-color: var(--primary);
    color: var(--dark);
    border-color: var(--primary);

}
.f-w{
    flex-wrap: wrap;
}
  /* skills */

  .card{
    margin-top: 20px;
    position: relative;
    min-width: 400px;
    max-width: 400px;
    background-color:var(--light) ;
    padding: 2rem;
    margin: 2rem 0px;
    margin-right: 30px;
    border-width: 0,5px;
    border-style: solid;
    border-radius: 10px;
    cursor: pointer;
    border-color: var(--pure);
    box-shadow: 14px 15px 14px -3px rgba(0, 0, 0, 0.75);
    transition: all 0.4s ease-in-out;
    }
    .card h3{
     font-size: 1.4rem;
     font-weight: 600;
     margin-bottom: 1rem;   
    }
    .card img{
        position: absolute;
        right: -25px;
        top: -25px;
    
    }
    .card h3::after{
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: var(--mix);
      
    }
    .card:hover{
        box-shadow: 23px 12px 14px -14px var(--dark);
        background: rgb(255, 120, 120);
        color: black;
        
    }
  .h3{
    margin-top: 15px;
  }
  .h2{
    margin-top: 10px;
  }
